import React from "react";
import { WrapPageElementBrowserArgs, WrapPageElementNodeArgs } from "gatsby";

import { DefaultSeo } from "../components/layout";

export const wrapPageElement = ({ element, props: { location } }: WrapPageElementBrowserArgs | WrapPageElementNodeArgs) => {
  return (
    <>
      <DefaultSeo location={location} />
      {element}
    </>
  );
};
